import axios from 'axios';
import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { Form, Button } from 'react-bootstrap';

import Message from '../../components/Message';
import Loader from '../../components/Loader';
import FormContainer from '../../components/FormContainer';

import {
  getProductDetails,
  updateProduct
} from '../../actions';

import { PRODUCT_UPDATE_RESET } from '../../constants';

const ProductEdit = ({
  match,
  history,
}) => {

  const productId = match.params.id;

  const [name, setName] = useState('');
  const [price, setPrice] = useState(0);
  const [image, setImage] = useState('');
  const [brand, setBrand] = useState('');
  const [category, setCategory] = useState('');
  const [countInStock, setCountInStock] = useState(0);
  const [description, setDescription] = useState('');
  const [uploading, setUploading] = useState(false);
  const [errorUploading, setErrorUploading] = useState(null);

  const dispatch = useDispatch();

  const { productDetails, productUpdate } = useSelector(state => state);

  const { loading, error, product } = productDetails;
  const { loading: loadingUpdate, error: errorUpdate, success: successUpdate } = productUpdate;

  useEffect(() => {
    if (successUpdate) {
      dispatch({
        type: PRODUCT_UPDATE_RESET,
      });
      history.push('/admin/products');
    }
    else {
      if (!product.name || product._id !== productId) {
        dispatch(getProductDetails(productId));
      } else {
        setName(product.name);
        setPrice(product.price);
        setImage(product.image);
        setBrand(product.brand);
        setCategory(product.category);
        setCountInStock(product.countInStock);
        setDescription(product.description);
      }
    }
  }, [product, productId, successUpdate, dispatch, history]);

  const uploadFileHandler = async e => {
    const file = e.target.files[0];
    const formData = new FormData();
    formData.append('image', file);
    setErrorUploading(null);
    setUploading(true);
    try {
      const config = {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
      };

      const { data } = await axios.post('/api/uploads', formData, config);
      setImage(data);
    } catch (error) {
      console.log(error);
      setErrorUploading(error.message);
    }
    finally {
      setUploading(false);
    }
  };

  const submitHandler = e => {
    e.preventDefault();
    dispatch(updateProduct({
      _id: product._id,
      name,
      price,
      brand,
      category,
      image,
      countInStock,
      description,
    }));
  };

  return (
    <>
      <Link
        to="/admin/products"
        className="btn btn-light my-3"
      >
        Go Back
      </Link>
      <FormContainer>
        <h1>Edit Product</h1>
        {errorUpdate && <Message variant="danger">{errorUpdate}</Message>}
        {loading || loadingUpdate
          ? <Loader />
          : error
            ? <Message variant="danger">{error}</Message>
            : (
              <Form onSubmit={submitHandler}>
                <Form.Group controlId="name">
                  <Form.Label>Name</Form.Label>
                  <Form.Control
                    type="name"
                    value={name}
                    required
                    placeholder="Enter your name"
                    onChange={(e) => setName(e.target.value)}
                  ></Form.Control>
                </Form.Group>
                <Form.Group controlId="price">
                  <Form.Label>Price</Form.Label>
                  <Form.Control
                    type="number"
                    value={price}
                    required
                    placeholder="Enter price"
                    onChange={(e) => setPrice(e.target.value)}
                  ></Form.Control>
                </Form.Group>
                <Form.Group controlId="image">
                  <Form.Label>Image</Form.Label>
                  <Form.Control
                    type="text"
                    value={image}
                    required
                    placeholder="Enter image URL"
                    onChange={(e) => setImage(e.target.value)}
                  ></Form.Control>
                  <Form.File
                    id="image-file"
                    label="Choose file"
                    custom onChange={uploadFileHandler}
                  ></Form.File>
                  {errorUploading && <Message variant="danger">{errorUploading}</Message>}
                  {uploading && <Loader />}
                </Form.Group>
                <Form.Group controlId="brand">
                  <Form.Label>Brand</Form.Label>
                  <Form.Control
                    type="text"
                    value={brand}
                    required
                    placeholder="Enter brand"
                    onChange={(e) => setBrand(e.target.value)}
                  ></Form.Control>
                </Form.Group>
                <Form.Group controlId="category">
                  <Form.Label>Category</Form.Label>
                  <Form.Control
                    type="text"
                    value={category}
                    required
                    placeholder="Enter category"
                    onChange={(e) => setCategory(e.target.value)}
                  ></Form.Control>
                </Form.Group>
                <Form.Group controlId="countInStock">
                  <Form.Label>Count In Stock</Form.Label>
                  <Form.Control
                    type="text"
                    value={countInStock}
                    required
                    placeholder="Enter count in stock"
                    onChange={(e) => setCountInStock(e.target.value)}
                  ></Form.Control>
                </Form.Group>
                <Form.Group controlId="description">
                  <Form.Label>Description</Form.Label>
                  <Form.Control
                    as="textarea"
                    rows={4}
                    value={description}
                    required
                    placeholder="Enter description"
                    onChange={(e) => setDescription(e.target.value)}
                  ></Form.Control>
                </Form.Group>
                <Button
                  type="submit"
                  variant="primary"
                >
                  Update
                </Button>
              </Form>
            )
        }
      </FormContainer>
    </>
  );
};

export default ProductEdit;
